body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background-color: #2f2f2f;
    color: white;
}

h1 {
    width: 100%;
    text-align: center;
    font-weight: 100;
    font-size: 50px;
    letter-spacing: 2.2px;
}

.square {
    background: #2f2f2f;
    border: none;
    box-shadow: 0px 0px 7px rgb(104, 104, 104);
    float: left;
    font-size: 44px;
    font-weight: bold;
    line-height: 100px;
    height: 100px;
    margin-right: 2px;
    margin-top: 2px;
    padding: 0;
    text-align: center;
    width: 100px;
    border-radius: 5px;
    color: white;
}

.square:hover {
    background: #272727;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: flex;
    flex-direction: row;
    padding: 10px;
    box-shadow: 0 0 6px rgb(34, 34, 34);
}

.game-info {
    margin-left: 20px;
    padding-left: 10px;
    border-left: 3px solid rgb(255, 255, 255);
}

.status-bar {
    font-size: 25px;
    margin-left: 25px;
}

.turn-counter {
    background:none;
    color: white;
    min-width: 75px;
    text-align: left;
    margin: 4px auto;
}

#root {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
}